<template>
	<section>
		<img v-lazy="imgUrl" class="topbanner">
		<div class="tips">
			<div class="tip-icon"></div>
			<ul class="tip-detail">
				<li>1. </li>
				<li>2. </li>
				<li>3. </li>
				<li>4. </li>
				<li>5. </li>
			</ul>
		</div>
		<router-link to="/chosefood" class="calculate-link"></router-link>
	</section>
</template>

<script>
export default {
	data () {
		return {
			imgUrl: '/static/topbanner1.png',
			title:''
		}
	},
  	head: {
		title: function () {
			return {
				inner: this.title,
				separator: ' ', 
	    		complement: ' '
			}
		}
	}
}
</script>


